.footer {
  background-image: url(../images/footermono.png);
  height: 200px;
  background-position: center;
  background-size: cover;

  @include onTABS {
    height: 400px;
    background-image: url(../images/footertab.png);
  }

  @include onDesktop {
    height: 460px;
    background-image: url(../images/footer.png);
  }

}
